export default {
	data() {
		return {
			tabbar: [{
				name: '推荐套餐',
				list: [{
					name: '肉类套餐1',
					id: '0',
					price: '328.00',
					num1: '4',
					num2: '500'
				}]
			}, {
				name: '团购套餐',
				list: [{
					name: '蔬菜套餐1',
					id: '0',
					price: '50.00',
					num1: '1',
					num2: '499'
				}, {
					name: '蔬菜套餐2',
					id: '1',
					price: '98.00',
					num1: '0',
					num2: '999'
				}]
			}],
			currentTab: 0, //预设当前项的值
			scrollTop: 0, //tab标题的滚动条位置
			num: 0,
			isShowShopCar:false
		}
	},
	onLoad: function(options) {

	},
	methods: {
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			let cur = e.currentTarget.dataset.current;
			if (this.currentTab == cur) {
				return false;
			} else {
				this.currentTab = cur;
				this.checkCor();
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			let that = this;
			//这里计算按照实际情况进行修改，动态数据要进行动态分析
			//思路：窗体高度/单个分类高度 200rpx 转px计算 =>得到一屏幕所显示的个数，结合后台传回分类总数进行计算
			//数据很多可以多次if判断然后进行滚动距离计算即可
			if (that.currentTab > 7) {
				that.scrollTop = 500
			} else {
				that.scrollTop = 0
			}
		}
	}
}
